import React from 'react'
import './App.css'
import pokemon from './pokemon.json'

function App() {
  return (
    <div
      style={{
        margin: 'auto',
        marginLeft: '30px',
        padding: '0.75rem',
        paddingLeft: '1.2rem',
        border: '1px solid',
      }}>
      <h1 className='title'>Hello</h1>
      <table>
        <thead>
          <tr>
            <th>Name</th>
            <th>In japanese</th>
            <th>Type</th>
          </tr>
        </thead>
        <tbody>
          {pokemon.slice(4, 20).map((element) => (
            <tr key={element.id}>
              <td>{element.name.english}</td>
              <td>{element.name.japanese}</td>
              <td>{element.type.join(' & ')}</td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  )
}

export default App
